:root {
  --background: #2d2d2d;
  --foreground: #e0e0e0;
  
  --primary-blue: #007bff;
  --primary-blue-hover: #0056b3;
  
  --status-using: #28a745;
  --status-keeping: #ffc107;
  --status-terminated: #dc3545;

  --user-conan-bg: #4a5a8a;
  --user-chaeji-bg: #b83a2a;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background);
  color: var(--foreground);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #2d2d2d;
}

::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #777;
}
